@import url(https://fonts.googleapis.com/css?family=Karla&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

div#root {
  height: calc(100vh - 150px); }

body {
  overflow: hidden;
  font-family: 'Karla', sans-serif;
  background-color: #EADCD2; }
  body .home {
    padding: 30px 100px;
    color: white;
    margin: 45px 30px;
    height: 100%;
    background-color: #CFA280;
    display: flex;
    flex-direction: column;
    justify-content: space-between; }
    body .home .header {
      font-size: 40px;
      left: 40px;
      position: relative;
      flex-direction: column;
      display: flex;
      line-height: 8px; }
    body .home .switcher {
      display: flex;
      position: absolute;
      border-radius: 30px;
      right: 120px;
      top: 100px;
      width: 130px;
      height: 40px;
      background-color: #F0C597;
      cursor: pointer; }
      body .home .switcher .btn {
        width: 46%;
        height: 100%;
        position: relative;
        left: 4px; }
        body .home .switcher .btn .choose {
          background: #EADCD2;
          border-radius: 35px;
          color: #CFA280; }
        body .home .switcher .btn p {
          text-align: center;
          position: relative;
          padding: 5px 0;
          top: -12px; }
    body .home .content {
      position: relative;
      display: flex;
      margin-bottom: 40px; }
      body .home .content .items {
        cursor: pointer;
        list-style: none;
        font-size: 28px;
        line-height: 55px; }
      body .home .content .content-right {
        font-size: 16px;
        padding: 0 80px;
        position: relative;
        height: 200px;
        width: 415px;
        overflow: scroll;
        top: -20px; }
        body .home .content .content-right .skills {
          font-size: 20px;
          line-height: 35px; }
        body .home .content .content-right .experience {
          font-size: 18px;
          line-height: 35px; }
        body .home .content .content-right .contact {
          font-size: 18px;
          line-height: 35px;
          text-align: left; }
          body .home .content .content-right .contact a {
            color: white; }
    body .home .go-resume {
      cursor: pointer;
      background-color: #F0C597;
      color: white;
      font-size: 20px;
      width: 350px;
      height: 45px;
      border: none;
      display: flex;
      justify-content: space-around; }

@media (max-width: 910px) {
  .home .content-left .items {
    display: none; }
  .home .content-left a {
    position: absolute;
    bottom: 15px; }
  .home .content-right {
    padding: 0; } }

@media (max-width: 700px) {
  .home .header {
    font-size: 30px !important;
    top: 40px; }
  .home .switcher {
    right: 60px !important; } }

